import React, {useEffect, useState} from 'react';
import Button from 'react-bootstrap/Button';
import { connect } from 'react-redux';
import { follow } from '../actions/follow';

function ProfileHeader({sessionUser, isAuthenticated, username, following, followers, follow, id}) {
    const [followed, setFollowed] = useState(false);
    const [alreadyFollowing, setAlreadyFollowing] = useState(false);
    const [loading, setLoading] = useState(false);
    
    const handleClick = () => {
        setFollowed(!followed);
        follow(id)
        console.log(alreadyFollowing)
    }

    useEffect(() => {
        if (sessionUser.follows !== undefined) {
            setAlreadyFollowing(sessionUser.follows.includes(id));
        }
        
        console.log(followed)
        setLoading(true);
    }, []);

    useEffect(() => {
        alreadyFollowing && setFollowed(true);
    }, [alreadyFollowing])

    const AuthFollowing = (
        <>
        {followed ? 
            <Button variant="outline-primary" onClick={handleClick} active> Unfollow </Button> :
            <Button variant="outline-primary" onClick={handleClick}> Follow</Button>
        }
        </>
    )

    const Auth = (
        <>
        {followed ? 
            <Button variant="outline-primary" onClick={handleClick}> Unfollow </Button> :
            <Button variant="outline-primary" onClick={handleClick} active> Follow</Button>
        }
        </>
    )

    const FollowingNums = (
        <>
            <p>Followers: <p> {followed ? followers : followers - 1 }</p></p> 
        </>
    )

    const Nums = (
        <>
            <p>Followers: <p> {followed ? followers + 1 : followers }</p></p> 
        </>
    )

    const userProfile = (
        <>
        <div className="profile_container">
        <div className="profile_header">
            <div className="profile_image_container">
                <div className="profile_image">
                    <div className="image_circle">
                    <div className="inner_image"></div>
                    <div className="inner_image2"></div>
                </div>
                </div>
            </div>
            <div className="profile_content">
                <div className="profile_username">
                    <p>{username}</p>
                </div>
                <div className="follower_following">
                    <p>Following: <p> {following}</p></p>
                    {alreadyFollowing ? FollowingNums : Nums}
                </div>
            </div>
        </div>
        {isAuthenticated && sessionUser.id !== id && <div className="follow_button_container">
            {alreadyFollowing ? AuthFollowing : Auth}
        </div>}
    </div>
    <div className="break_out"/>
    </>
    )
    
    return (
        <> 
        {loading &&
       userProfile}
        </>
    )
}


const mapStateToProps = state => ({
    sessionUser: state.user,
    isAuthenticated: state.auth.isAuthenticated
})

export default connect(mapStateToProps, {follow})(ProfileHeader);